.Watchlist {

    padding-left: 3rem;
    /* padding-right: 6rem; */
    padding-bottom: 2%;
    width: 100%;
    height: 20rem;
    color: white;
    margin-top: 2%;
    overflow-x: scroll;
    overflow-y: hidden; /* Disable vertical scrolling */
    white-space: nowrap;
    scrollbar-width: none;
    -ms-overflow-style: none;
  }
  
  .ItemsPanel::-webkit-scrollbar {
    display: none;
  }
  
  .category {
    margin-left: 3px;
  }
  
  .slider {
    display: flex;
    flex-direction: row;
    height: 100%;
    /* justify-content: center; */
  }
  
  .slider > * {
    flex: 0 0 auto;
    margin-right: 1rem;
    overflow-y: hidden; /* Disable vertical scrolling for individual items */
  }
  